import * as React from 'react';
import { ThemeContextInterface } from '~src/types/context';
import { themes } from '../../_styles/theme';
import useTheme from '../Hooks/useTheme';

const defaultThemeContext: ThemeContextInterface = {
  theme: 'light',
  setTheme: () => null,
  themes: themes,
};

export const ThemeContext = React.createContext<ThemeContextInterface | null>(defaultThemeContext);

const ThemeWrapper: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [theme, setTheme] = useTheme();
  return (
    <ThemeContext.Provider value={{ theme, setTheme, themes }}>
      <React.Fragment>{children}</React.Fragment>
    </ThemeContext.Provider>
  );
};

export default ThemeWrapper;
