import * as React from 'react';
import Context from '../../src/context/Context';
import type { GatsbyBrowser } from 'gatsby';
//import { defaultOptions } from '../defaultOptions';

const wrapPageElement: GatsbyBrowser['wrapPageElement'] = ({ element, props } /* , pluginOptions */) => {
  /* 	if (!props) {
    return;
  } */
  const { locale } = props.pageContext;
  const { messages } = props.pageContext.localeContext;
  //localStorage.setItem('pageLocale', locale);

  /*
  const options = {
  ...defaultOptions,
  ...plugingOptions };


  localStorage.setItem('localeContext', JSON.stringify(options));
  */

  return (
    <Context pageLocale={locale} messages={messages} {...props}>
      {element}
    </Context>
  );
};

export default wrapPageElement;
