import { useEffect } from 'react';
import useLocalStorage from './useLocaleStorage';
import { getLangDir } from 'rtl-detect';
//import browserLang from 'browser-lang';
//import getDefaultLocale from '~gatsby/utils/getDefaultLocale';

function useLocale(pageLocale?: string) {
  const initialLocale = pageLocale ? pageLocale : 'en';

  // Use our useLocalStorage hook to persist state through a page refresh.
  // Read the recipe for this hook to learn more: usehooks.com/useLocalStorage
  const [locale, setLocale] = useLocalStorage('locale', initialLocale);

  //const pageLocale = typeof window !== `undefined` ? localStorage.getItem('pageLocale') : null;

  // If lang is defined use it, otherwise fallback to en.
  // This allows user to override OS level setting on our website.
  //const activeLocale = pageLocale ? pageLocale : locale;

  //SEEMS UNNECESSARY: test and remove
  /* 	if (pageLocale && pageLocale !== locale) {
		setLocale(pageLocale);
	} */
  // Using timeout to match with animation
  /* 	if (pageLocale && pageLocale !== locale) {
		setTimeout(() => {
			setLocale(pageLocale);
		}, 70);
	} */
  if (pageLocale && pageLocale !== locale) {
    setLocale(pageLocale);
  }
  useEffect(() => {
    /* 	console.log('hook initalized', locale); */
    //console.log('activeLocale in useEffect is:', activeLocale);
    /* 		window.setTimeout(() => {
			const langDir = rtlDetect.getLangDir(locale);
			document.documentElement.dir = langDir;
			document.documentElement.lang = locale;
		}, 10); */

    const langDir = getLangDir(locale);
    document.documentElement.dir = langDir;
    document.documentElement.lang = locale;

    /* 		if (pageLocale && pageLocale !== locale) {
			setTimeout(() => {
				setLocale(pageLocale);
			}, 50);
		} */
    /*
		// Change font for rtl Languages
		const body = document.querySelector('body') as HTMLElement;
		const selects = document.querySelectorAll('select');

		// Handle fonts for different languages
		if (body && langDir == 'rtl') {
			body.style.fontFamily = 'Sahel , sans-serif';
			selects.forEach(select => {
				select.style.fontFamily = 'Sahel , sans-serif';
			});
		} else {
			body.style.fontFamily = 'Tahoma , sans-serif';
			selects.forEach(select => {
				select.style.fontFamily = 'Tahoma , sans-serif';
			});
		} */

    return () => {
      /* 	console.log('Unmounted'); */
      // Using timeout to match with animation
      /* 		if (pageLocale && pageLocale !== locale) {
				setLocale(pageLocale);
			} */
    };
  }, [locale]); // Only re-call effect when value changes

  // Return enabled state and setter
  return [locale, setLocale];
}

export default useLocale;
